import React from 'react';
import moment from 'moment';

import { clearDrinks } from '../actions/drinks';

import Drink from './drink';

export default (props) => {

  const sd = Math.ceil(props.sd * 10) / 10;
  const first = moment(props.startTimestamp).format('h:mm a');

  return (
    <div className="view drinks">
      <p className="how-much-since">You've had <b>{sd}</b> standard drinks since <b>{first}</b>.</p>

      <div className="drinks-list">
        {props.drinks.map(drink => <Drink key={drink.timestamp} {...drink} />)}
      </div>

      <p>
        <button className="btn" onClick={clearDrinks}>Clear All</button>
      </p>
    </div>
  );
};



